___CSS_LOADER_EXPORT___.push([module.id, `:root {
  /* --bright: #fefae0;
  --light-mid: #dda15e;
  --dim-mid: #bc6c25;
  --dark: #283618; */

  --bright: #e6f9af;
  /* --light-mid: #eeaf68; */
  --dim-mid: #17bebb;
  --dark: #161925;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  height: 100vh;
  background: white;

  display: grid;
  grid-template-rows: 1fr auto;
  place-items: center;
}

#slider-wrapper {
  width: 600px;
  background: var(--dark);
  border-radius: 5px;

  display: grid;
  grid-template-rows: 430px;
  grid-template-columns: 1fr;
  place-items: center;

  #inner-element-wrapper {
    grid-row: 1 / 2;
    grid-column: 1 / 2;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;

    #left-arrow,
    #right-arrow {
      width: 30px;
      z-index: 2;
      background: var(--dim-mid);
      transition: background 300ms;
      padding: 5px;
      border-radius: 5px;
    }

    #left-arrow:hover,
    #right-arrow:hover {
      background: var(--bright);
    }

    #left-arrow {
      grid-column: 1 / 2;
    }

    #image-wrapper {
      grid-column: 2 / 3;

      width: 500px;
      height: 300px;
      border-radius: 5px;
      overflow: hidden;
      position: relative;

      display: flex;
      justify-content: center;
      align-items: center;
    }

    .image {
      position: absolute;
      transition: 500ms ease-in-out;
      width: 500px;
      object-fit: scale-down;
    }

    #right-arrow {
      grid-column: 3 / 4;
    }
  }

  #nav-dots {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    align-self: end;
    position: relative;
    bottom: 17px;

    display: flex;
    gap: 5px;

    .dot {
      width: 15px;
      height: 15px;
      background: var(--dim-mid);
      border-radius: 50%;
      transition: background 200ms;

      &:hover:not([data-nav-dot='active']) {
        cursor: pointer;
        /* background: var(--light-mid); */
      }
    }

    [data-nav-dot='active'] {
      background: var(--bright);
    }
  }
}

footer {
  width: 100vw;
  padding: 20px;
  letter-spacing: 0.75px;
  background: var(--dark);
  color: var(--dim-mid);

  display: flex;
  flex-direction: column;
  gap: 2px;

  a:link,
  a:visited,
  a:active {
    color: var(--dim-mid);
  }

  a:hover {
    color: var(--bright);
  }
}
